var React = require('react');
var ReactDOM = require('react-dom');

var Header = require('../component/header.js');
var NextButton = require('../component/next-button.js');

var chartStyle = {
  width: '800',
  height: '400'
};

var UserLocation = React.createClass({
  getInitialState: function() {
    return {
      data: {
        a: 0,
        b: 0,
        c: 0,
        d: 0,
        e: 0,
        f: 0
      }
    }
  },

  loadDataFormServer: function() {
    $.ajax({
      type: 'get',
      url: '/userLocation',
      dataType: 'json',
      cache: true,
      success: function(result){
        this.setState({data: result});
      }.bind(this),
      error: function(xhr, status, err){
        console.log(this.props.url, status, err);
      }.bind(this)
    });
  },

  componentDidMount: function() {
    // domecharts
    this.loadDataFormServer();
    var myChart = echarts.init(document.getElementById('chart-container')); 
    
    option = {
      title : {
        text: 'iphone',
        subtext: '',
        x:'center'
      },
      tooltip : {
        trigger: 'item'
      },
      legend: {
        orient: 'vertical',
        x:'left',
        data:['iphone3']
      },
      dataRange: {
        min: 0,
        max: 2500,
        x: 'left',
        y: 'bottom',
        text:['',''],           // 
        calculable : true
      },
      toolbox: {
        show: true,
        orient : 'vertical',
        x: 'right',
        y: 'center',
        feature : {
          mark : {show: true},
          dataView : {show: true, readOnly: false},
          restore : {show: true},
          saveAsImage : {show: true}
        }
      },
      roamController: {
        show: true,
        x: 'right',
        mapTypeControl: {
          'china': true
        }
      },
      series : [{
        name: 'iphone3',
        type: 'map',
        mapType: 'china',
        roam: false,
        itemStyle:{
          normal:{label:{show:true}},
          emphasis:{label:{show:true}}
        },
        data:[
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)},
          {name: '',value: Math.round(Math.random()*1000)}
        ]
      }]
    };

    // echarts 
    myChart.setOption(option); 
  },

  render: function() {
    return (
      <div>
        <Header>UserLocation</Header>
        <div className="ui one colum centered grid">
          <div className="colum">
            <div id="chart-container" style={chartStyle}></div>
          </div>
          <div className="ui centered row">
            <NextButton url="/other"></NextButton>
          </div>
        </div>
      </div>
    );
  }
});

module.exports = UserLocation;


